let collectionSlidersArray = []

function collectionSliders() {
    $(".slider-parent").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        collectionSlidersArray[index] = new Swiper(`.slider-parent[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 3,
            spaceBetween: 20,
            navigation: {
                nextEl: `.slider-parent[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider-parent[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider-parent[data-slider-id="${index}"] .swiper-pagination`,
                type: 'custom',
                clickable: true,
                renderCustom: function (swiper, current, total) {
                    return `
                    <span class="swiper-pagination-title">${current}</span>
                    <span class="swiper-pagination-title"> ${$(el).find(".swiper-pagination").attr("data-phrase")}</span>
                    <span class="swiper-pagination-title">${total}</span>
                `
                },
            },
            breakpoints: {
                320 : {
                    slidesPerView: "auto",
                    spaceBetween: 15,
                },
                992 : {
                    slidesPerView: 2,
                    spaceBetween: 20,
                },
                1250 : {
                    slidesPerView: 3,
                    spaceBetween: 20,
                }
            }
        })
    })
}

collectionSliders()
